import React, {Fragment} from "react";
import {withStyles, withWidth} from "@material-ui/core";
import PropTypes from "prop-types";
import { propTypes } from "react-bootstrap/esm/Image";

const styles = ({
    container: {
        width : "100%",
        height: "97.7vh",
        paddingTop: "150px",
        paddingRight: "15vw",
        paddingLeft: "15vw"
    },
    bigLetter: {
        fontSize: "30px",
        fontWeight: "100px"
    },
    smallLetter: {
        fontFamily: "initial",
        fontSize: "20px",
        fontWeight: "100px"
    },
    Block1:{
        textAlign: "center",
        display: 'inline-block',
        width: '45%',
    },
    Block2:{
        textAlign: "center",
        display: 'inline-block',
        width: '45%',
        float: 'right'
    },
    Block3:{
        paddingBottom: "45px",
        paddingTop: "55px",
        margin: 'auto',
        textAlign: "center",
        width: '70%',
    }
})

function Product (props)
{
    const {classes} = props;
    return(
        <Fragment>
            <div className = {classes.container}>
                <div  className = {classes.Block1}>
                    <div>
                        <span className = {classes.bigLetter}>Turnkey deployment</span>
                    </div>
                    <div>
                        <span className = {classes.smallLetter}>No need for expensive customization <br/> or a dedicated IT team.This solution <br/> comes ready to go with everything <br/> an organisation needs</span>
                    </div>
                </div> 

                <div  className = {classes.Block2}>
                    <div>
                        <span className = {classes.bigLetter}>Search</span>
                    </div>
                    <div>
                        <span className = {classes.smallLetter}>Federated search allows you to get quick results <br/> for exactly as you type it but aslo sramt search <br/> (that was intended and you could read it)</span>
                    </div>
                </div> 

                <div  className = {classes.Block3}>
                    <div>
                        <span className = {classes.smallLetter}>By default, the platform comes preconfigured with a configuration suitable <br/> for 90% of organisations. Permissions, Space, Groups, Multiple Content <br/> Types are awaiting you after you click "GET STARTED"</span>
                    </div>
                </div> 

                <div  className = {classes.Block1}>
                    <div>
                        <span className = {classes.bigLetter}>Communicate</span>
                    </div>
                    <div>
                        <span className = {classes.smallLetter}>Whether it's News, Video, Blog, Inbox, <br/> a document or an urgent notification, <br/> you can spark meaningufull communications.</span>
                    </div>
                </div> 

                <div  className = {classes.Block2}>
                    <div>
                        <span className = {classes.bigLetter}>Collaborate</span>
                    </div>
                    <div>
                        <span className = {classes.smallLetter}>Which tools do your employees need <br/> to do their job? To communicate information? <br/> Make sure they know and stay engaged.</span>
                    </div>
                </div> 

                <div  className = {classes.Block3}>
                    <div>
                        <span className = {classes.smallLetter}>Security - Being hosted on AWS USA and Europe, <br/> We protect your data and ensure compliance such as SOC1 and SOC2</span>
                    </div>
                </div> 

                <div  className = {classes.Block1}>
                    <div>
                        <span className = {classes.bigLetter}>Gamification</span>
                    </div>
                    <div>
                        <span className = {classes.smallLetter}>Yes,that is part of the deal too.<br/> Employees like to be incentivized.<br/> Make it possible.</span>
                    </div>
                </div> 

                <div  className = {classes.Block2}>
                    <div>
                        <span className = {classes.bigLetter}>Analyze</span>
                    </div>
                    <div>
                        <span className = {classes.smallLetter}>Get the insights you need about <br/> what's working and what's not.</span>
                    </div>
                </div> 
            </div>
        </Fragment>
    ) 
}

Product.propTypes = {
    classes: PropTypes.object.isRequired
}
export default withWidth()(withStyles(styles, { withTheme: true })(Product));