import React, { useState, useCallback, useRef, Fragment, Component } from "react";
import PropTypes from "prop-types";
import {
  FormHelperText,
  TextField,
  Button,
  Checkbox,
  Typography,
  FormControlLabel,
  withStyles,
  Snackbar
} from "@material-ui/core";
import MuiAlert from '@material-ui/lab/Alert'
import FormDialog from "../../../shared/components/FormDialog";
import HighlightedInformation from "../../../shared/components/HighlightedInformation";
import ButtonCircularProgress from "../../../shared/components/ButtonCircularProgress";
import VisibilityPasswordTextField from "../../../shared/components/VisibilityPasswordTextField";
import axios from 'axios';
import { Router } from "@material-ui/icons";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const styles = (theme) => ({
  link: {
    transition: theme.transitions.create(["background-color"], {
      duration: theme.transitions.duration.complex,
      easing: theme.transitions.easing.easeInOut,
    }),
    cursor: "pointer",
    color: theme.palette.primary.main,
    "&:enabled:hover": {
      color: theme.palette.primary.dark,
    },
    "&:enabled:focus": {
      color: theme.palette.primary.dark,
    },
  },
});

function RegisterDialog(props) {
  const { setStatus, theme, onClose, openTermsDialog, status, classes } = props;
  const [isLoading, setIsLoading] = useState(false);
  const [hasTermsOfServiceError, setHasTermsOfServiceError] = useState(false);
  const [isPasswordVisible, setIsPasswordVisible] = useState(false);
  const registerTermsCheckbox = useRef();
  const registerPassword = useRef();
  const registerPasswordRepeat = useRef();
  const firstName = useRef();
  const lastName = useRef();
  const email = useRef();
  const subdomain = useRef();
  const [notiopen, setnotiopen] = useState(false);

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setnotiopen(false);
  };

  const register = useCallback(() => {
    if (!registerTermsCheckbox.current.checked) {
      setHasTermsOfServiceError(true);
      return;
    }
    if (
      registerPassword.current.value !== registerPasswordRepeat.current.value
    ) {
      setStatus("passwordsDontMatch");
      return;
    }
    else{
      if(registerPassword.current.value.length > 6){
        axios({
          method: "POST",
          url: "http://3.139.50.101:4000/userControl/create-user",
          data: {
            firstname: firstName.current.value,
            lastname: lastName.current.value,
            email: email.current.value,
            password: registerPassword.current.value,
            subdomain: subdomain.current.value,
            role: "user",
            permission: "deactive"
          }
        }).then((response)=>{
          console.log(response)
          if(response.data){
            setnotiopen(true);
            window.location.href = "http://start.azintranet.com"

            
          }
          else{
            console.log("Error")
          }
        })
        .catch(err => {
          console.log(err.response.status);
          if(err.response.status === 400)
          {
          setStatus("emailexist");
          return;
        }

        if(err.response.status === 300)
          {
          setStatus("subdomainexist");
          return;
        }
        }
        )
      }
      else{
        setStatus("passwordTooShort");
        return;
      }
    }
    setStatus(null);
    setIsLoading(true);
    setTimeout(() => {
      setIsLoading(false);
    }, 1500);
  }, [
    setIsLoading,
    setStatus,
    setHasTermsOfServiceError,
    registerPassword,
    registerPasswordRepeat,
    registerTermsCheckbox,
  ]);

  return (
    <FormDialog
      loading={isLoading}
      onClose={onClose}
      open
      headline="Register"
      onFormSubmit={(e) => {
        e.preventDefault();
        console.log(firstName.current.value, registerPasswordRepeat.current.value)
        register();
      }}
      hideBackdrop
      hasCloseIcon
      content={
        <Fragment>
            <Snackbar open={notiopen} autoHideDuration={6000} onClose={handleClose}>
              <Alert onClose={handleClose} severity="success">
                You are registered into this website successfully!
              </Alert>
            </Snackbar>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            error={status === "Please input your firstname"}
            label="First Name"
            inputRef={firstName}
            autoFocus
            autoComplete="off"
            type="text"
            onChange={() => {
              if (status === "Please input your firstname") {
                setStatus(null);
              }
            }}
            FormHelperTextProps={{ error: true }}
          />
           <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            error={status === "Please input your lastname"}
            label="Last Name"
            inputRef = {lastName}
            autoComplete="off"
            type="text"
            onChange={() => {
              if (status === "Please input your lastname") {
                setStatus(null);
              }
            }}
            FormHelperTextProps={{ error: true }}
          />
           <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            error={status === "invalidEmail" || status === "emailExist"}
            label="Email Address"
            inputRef = {email}
            autoComplete="off"
            type="email"
            onChange={() => {
              if (status === "invalidEmail") {
                setStatus(null);
              }
            }}
            helperText={(() => {
              if(status === "emailexist")
              {
                return "email already exist"
              }
              return null;
            })()}
            FormHelperTextProps={{ error: true }}
          />
          <VisibilityPasswordTextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            error={
              status === "passwordTooShort" || status === "passwordsDontMatch"
            }
            label="Password"
            inputRef={registerPassword}
            autoComplete="off"
            onChange={() => {
              if (
                status === "passwordTooShort" ||
                status === "passwordsDontMatch"
              ) {
                setStatus(null);
              }
            }}
            helperText={(() => {
              if (status === "passwordTooShort") {
                return "Create a password at least 6 characters long.";
              }
              if (status === "passwordsDontMatch") {
                return "Your passwords dont match.";
              }
              return null;
            })()}
            FormHelperTextProps={{ error: true }}
            isVisible={isPasswordVisible}
            onVisibilityChange={setIsPasswordVisible}
          />
          <VisibilityPasswordTextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            error={
              status === "passwordTooShort" || status === "passwordsDontMatch"
            }
            label="Repeat Password"
            inputRef={registerPasswordRepeat}
            autoComplete="off"
            onChange={() => {
              if (
                status === "passwordTooShort" ||
                status === "passwordsDontMatch"
              ) {
                setStatus(null);
              }
            }}
            helperText={(() => {
              if (status === "passwordTooShort") {
                return "Create a password at least 6 characters long.";
              }
              if (status === "passwordsDontMatch") {
                return "Your passwords dont match.";
              }
            })()}
            FormHelperTextProps={{ error: true }}
            isVisible={isPasswordVisible}
            onVisibilityChange={setIsPasswordVisible}
          />
           <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            error={status === "invalidEmail" || status === "subdomainexist"}
            label="Subdomain"
            inputRef = {subdomain}
            autoComplete="off"
            type="text"
            onChange={() => {
              if (status === "invalidEmail") {
                setStatus(null);
              }
            }}
            helperText={(() => {
              if (status === "subdomainexist") {
                return "subdomain already exist";
              }
            })()}
            FormHelperTextProps={{ error: true }}
          />
          <FormControlLabel
            style={{ marginRight: 0 }}
            control={
              <Checkbox
                color="primary"
                inputRef={registerTermsCheckbox}
                onChange={() => {
                  setHasTermsOfServiceError(false);
                }}
              />
            }
            label={
              <Typography variant="body1">
                I agree to the
                <span
                  className={classes.link}
                  onClick={isLoading ? null : openTermsDialog}
                  tabIndex={0}
                  role="button"
                  onKeyDown={(event) => {
                    // For screenreaders listen to space and enter events
                    if (
                      (!isLoading && event.keyCode === 13) ||
                      event.keyCode === 32
                    ) {
                      openTermsDialog();
                    }
                  }}
                >
                  {" "}
                  terms of service
                </span>
              </Typography>
            }
          />
          {hasTermsOfServiceError && (
            <FormHelperText
              error
              style={{
                display: "block",
                marginTop: theme.spacing(-1),
              }}
            >
              In order to create an account, you have to accept our terms of
              service.
            </FormHelperText>
          )}
          {status === "accountCreated" ? (
            <HighlightedInformation>
              We have created your account. Please click on the link in the
              email we have sent to you before logging in.
            </HighlightedInformation>
          ) : (
            <HighlightedInformation>
              create your account
            </HighlightedInformation>
          )}
        </Fragment>
      }
      actions={
        <Button
          type="submit"
          fullWidth
          variant="contained"
          size="large"
          color="secondary"
          disabled={isLoading}
        >
          Register
          {isLoading && <ButtonCircularProgress />}
        </Button>
      }
    />
  );
}

RegisterDialog.propTypes = {
  theme: PropTypes.object.isRequired,
  onClose: PropTypes.func.isRequired,
  openTermsDialog: PropTypes.func.isRequired,
  status: PropTypes.string,
  setStatus: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(RegisterDialog);
