import React, { memo } from "react";
import PropTypes from "prop-types";
import { Switch, BrowserRouter,hashRouter, Route } from "react-router-dom";
import {withRouter} from 'react-router'
import PropsRoute from "../../shared/components/PropsRoute";
import Home from "./home/Home";
import Blog from "./blog/Blog";
import BlogPost from "./blog/BlogPost";
import RegisterDialog from "./register_login/RegisterDialog"
import Product from './product/product'
import Feature from './feature/FeatureSection';
import ContactUs from './footer/Footer'
import Pricing from './pricing/PricingSection'
import AboutUs from './aboutUs/aboutUs'
import LoginDialog from './register_login/LoginDialog'

function Routing(props) {
  const { blogPosts, selectBlog, selectHome, openRegisterDialog, onClose } = props;


  return (
    <hashRouter>
    <Switch>
      {blogPosts.map((post) => (
        <PropsRoute
          path={post.url}
          component={BlogPost}
          title={post.title}
          key={post.title}
          src={post.src}
          date={post.date}
          content={post.content}
          otherArticles={blogPosts.filter(
            (blogPost) => blogPost.id !== post.id
          )}
        />
      ))}
      <PropsRoute
        path="/features"
        component={Feature}
        selectBlog={selectBlog}
        blogPosts={blogPosts}
      />
      <PropsRoute
        path="/contact"
        component={ContactUs}
        selectBlog={selectBlog}
        blogPosts={blogPosts}
      />
      <PropsRoute
        path="/pricing"
        component={Pricing}
        selectBlog={selectBlog}
        blogPosts={blogPosts}
      />
      <PropsRoute
        path="/product"
        component={Product}
        selectBlog={selectBlog}
        blogPosts={blogPosts}
      />
       <PropsRoute
        path="/about"
        component={AboutUs}
        selectBlog={selectBlog}
        blogPosts={blogPosts}
      />
      <PropsRoute
        path="/login"
        component={LoginDialog}
        selectBlog={selectBlog}
        blogPosts={blogPosts}
      />
      <PropsRoute exact path="/" component={Home} selectHome={selectHome} openRegisterDialog = {openRegisterDialog} />
  
    </Switch>
    </hashRouter>
  );
}

Routing.propTypes = {
  blogposts: PropTypes.arrayOf(PropTypes.object),
  selectHome: PropTypes.func.isRequired,
  selectBlog: PropTypes.func.isRequired,
};

export default memo(Routing);
