import React, {Fragment} from "react";
import {withStyles, withWidth} from "@material-ui/core";
import PropTypes from "prop-types";
import { propTypes } from "react-bootstrap/esm/Image";

const styles = ({
    container: {
        width : "100%",
        height: "97.7vh",
        paddingTop: "150px",
        paddingRight: "15vw",
        paddingLeft: "15vw"
    },
    bigLetter: {
        fontSize: "35px",
        fontWeight: "200px"
    },
    smallLetter: {
        fontFamily: "initial",
        fontSize: "20px",
        fontWeight: "100px"
    },
})

function Product (props)
{
    const {classes} = props;
    return(
        <Fragment>
            <div className = {classes.container}>
                <div  className = {classes.Block1}>
                    <div>
                        <span className = {classes.bigLetter}>Azintranet: Helping you interact</span>
                    </div>
                    <div>
                        <span className = {classes.smallLetter}>Get this and that done, that's what it's all about.Work needs to<br/> get done one way or abother, might as well be in a fun and structured way<br/><br/><br/> While working for other Saas companies including intranet providers such as live, <br/> our cofounders decided to cut through the big corporate non-sense and offer you, our client,<br/> a no bulishit platform<br/><br/>Aren't you fed up of hefty setup fees, designer costs and high per user per month costs?<br/>And the minimum price that they want you to invest in order for them to consider you a client?<br/><br/>If you have been shopping for an intranet, you have heard all that.<br/><br/>Not here.</span>
                    </div>
                </div> 
            </div>
        </Fragment>
    ) 
}

Product.propTypes = {
    classes: PropTypes.object.isRequired
}
export default withWidth()(withStyles(styles, { withTheme: true })(Product));